.offer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  max-width: 878px;
  margin: 0 auto;
  padding-top: 17.8%;

  @media (max-width: $viewport--lg) {
    flex-direction: column;
    align-items: center;
    padding-top: 0;
    max-width: 100%;
  }
}

.offer__height {
  display: none;

  @media (max-width: $viewport--lg) {
    display: block;
    padding-bottom: 44.33%;  // соотношение высоты к ширине bg
  }

  @media (max-width: $viewport--md) {
    display: block;
    padding-bottom: 122%;  // соотношение высоты к ширине bg
  }
}

.offer__adv {
  position: relative;
  width: 350px;
  color: $color-default-white;

  @media (max-width: $viewport--lg) {
    display: flex;
    width: 100%;
    background-color: $color-default-black;
    background-image: url("../img/tablet/main-bg@1x.jpg");
    background-repeat: no-repeat;
    background-size: cover;

    @include retina {
      background-image: url("../img/tablet/main-bg@2x.jpg");
    }
  }

  @media (max-width: $viewport--md) {
    background-image: url("../img/mobile/main-bg@1x.jpg");

    @include retina {
      background-image: url("../img/mobile/main-bg@2x.jpg");
    }
  }
}

.offer__info {
  @media (max-width: $viewport--lg) {
    justify-self: center;
    margin: 0 auto;
  }
}

.offer__logo {
  @media (max-width: $viewport--lg) {
    position: absolute;
    top: 25px;
    left: 21px;
  }

  @media (max-width: $viewport--md) {
    left: 18px;
  }
}

.offer__info p:first-of-type {
  font-size: 46px;
  font-weight: 600;
  line-height: 1.4;
  letter-spacing: -0.004em;
  margin: 24px 0 24px 0;

  @media (max-width: $viewport--lg) {
    width: 404px;
    margin: 134px auto 20px 6px;
    font-size: 30px;
    line-height: 1.4;
  }

  @media (max-width: $viewport--md) {
    text-align: center;
    width: 288px;
    margin: 119px auto 20px auto;
  }
}

.offer__info p:last-of-type {
  font-size: 20px;
  font-weight: 300;
  line-height: 1.6;
  letter-spacing: -0.005em;
  color: $color-default-white;

  @media (max-width: $viewport--lg) {
    width: 400px;
    margin: auto;
    font-size: 18px;
    line-height: 1.8;
    text-align: center;
  }

  @media (max-width: $viewport--md) {
    width: 288px;
  }
}
